.schedule-card {
  display: flex;
  gap: 12px;
  align-items: stretch;
  border-radius: 14px;
  padding: 12px;
  background: linear-gradient(135deg, rgba(255,255,255,0.06), rgba(255,255,255,0.02));
  border: 1px solid rgba(255,255,255,0.06);
  box-shadow: 0 6px 18px rgba(10,10,10,0.06);
  transition: transform .18s ease, box-shadow .18s ease;
}
.schedule-card:hover { transform: translateY(-6px); box-shadow: 0 14px 28px rgba(10,10,10,0.12); }
.schedule-card .card-thumb { width: 110px; height: 80px; border-radius: 8px; background-size: cover; background-position: center; position: relative; flex: 0 0 110px; }
.schedule-card .card-thumb .sparkles { position: absolute; bottom: 6px; right: 8px; font-size: 14px; }
.schedule-card .card-thumb::after { content: ' '; position: absolute; inset: 0; border-radius: 8px; box-shadow: inset 0 0 18px rgba(255,255,255,0.02); pointer-events: none; }
.sparkles { animation: sparkle 2.8s infinite; }
@keyframes sparkle { 0% { transform: translateY(0) scale(1); opacity: .85 } 50% { transform: translateY(-3px) scale(1.08); opacity:1 } 100% { transform: translateY(0) scale(1); opacity:.85 } }
.schedule-card .card-body { display: flex; flex-direction: column; gap: 8px; min-width: 0; }
.schedule-card .card-header { display:flex; justify-content:space-between; gap: 8px; align-items: baseline; }
.schedule-card .card-title { margin:0; font-size: 1rem; color: #fff; white-space: nowrap; overflow: hidden; text-overflow: ellipsis;}
.schedule-card .card-platforms { display:flex; gap:6px; align-items:center; }
.platform-badge { padding: 4px 8px; border-radius: 999px; color: white; font-size: 12px; display:inline-flex; gap:5px; align-items:center; }
.card-meta { display:flex; gap: 12px; color: #d1d6dd; font-size: 0.9rem; }
.card-actions { display:flex; gap:6px; margin-top:6px; justify-content:flex-end; }
.btn.small { padding: 6px 8px; border-radius:8px; background: rgba(255,255,255,0.04); border: 1px solid rgba(255,255,255,0.06); color: #e6eef7; font-size: 12px; cursor: pointer; }
.btn.small.primary { background: linear-gradient(90deg, #a78bfa, #60a5fa); color: #fff; border: none; }
.btn.small.ghost { background: transparent; color: #fff; border: 1px dashed rgba(255,255,255,0.1); }
.btn.small.danger { background: linear-gradient(90deg,#ff7b7b,#ff4d4d); color: #fff; border: none; }
.schedule-card.paused { opacity: 0.75; filter: grayscale(.08); }
